.nav {
  padding-top: 2.9375rem;
  padding-bottom: 2.9375rem;
  margin-bottom: 1.875rem;
}
.nav__logo {
  display: inline-block;
  font-size: 0;
}
.nav__img {
  width: 8.125rem;
  height: 2.3125rem;
}
@media (min-width: 40em) {
  .nav__img {
    width: 10.4375rem;
    height: 3.0625rem;
  }
}
.nav-links {
  gap: 3.75rem;
}
@media (max-width: 39.9375em) {
  .nav-links {
    display: none;
  }
}
@media (min-width: 71.875em) {
  .nav-links {
    font-size: 0.875rem;
  }
}
.nav-menu * {
  display: block;
}
.nav-menu span {
  width: 30px;
  border: 1px solid #585989;
}
.nav-menu :not(:last-child) {
  margin-bottom: 8px;
}
@media (min-width: 40em) {
  .nav-menu {
    display: none;
  }
}